.affliate-cont {
  width: 100%;
  height: 100%;
  background-image: url("../../Assets/Images/affiliate/Base.png");
  background-size: 100% 100%;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  min-height: 60vh;
  text-align: center;
  padding: 0 5rem;
}

.vendor-title {
  font-size: 1.8rem;
  margin: 3rem 1rem;
  font-weight: 600;
}
.vendor-affiliate-detail {
  display: flex;
  font-weight: 600;
  font-size: 1.5rem;
  justify-content: space-between;
  cursor: pointer;
}
.vendor-affiliate-detail :nth-child(1) {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.vendor-affiliate-detail :nth-child(2) {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  gap: 1rem;
}
.affiliate-arrow {
  margin-top: 0.5rem;
}
#APmainCont {
  background: #fafafa;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  border-radius: 10px;
}
#APcontPart1 {
  text-align: center;
  font-size: 22px;
  font-weight: 500;
  padding: 3% 0% 2% 0%;
  margin-bottom: 10px;
}
.APpara {
  font-size: 18px;
  text-align: center;
  padding: 0% 10% 2% 10%;
  opacity: 75%;
  font-weight: 400;
}
.APorangeHead {
  font-size: 25px;
  font-weight: 700;
  color: #ff8d22;
  padding: 2% 0% 1% 0%;
  text-align: center;
}
.APBlackHead {
  font-size: 25px;
  font-weight: 700;
  color: black;
  padding: 0% 0% 2% 0%;
  text-align: center;
}
#JoinNow {
  background-color: #ff8d22;
  color: white;
  border: 0px;
  height: 70px;
  width: 440px;
  border-radius: 12px;
  font-size: 14px;
  font-weight: 700;
  margin: 1%;
}
#APbuttonCont {
  padding: 2% 0% 7% 0%;
}

@media screen and (width: 912px) {
  .affliate-cont {
    min-height: 30%;
    padding: 0 2rem;
  }

  .vendor-title {
    margin: 1rem 1rem;
    font-weight: 400;
    font-size: 15px;
  }

  .vendor-affiliate-detail {
    flex-direction: column;
  }

  .vendor-affiliate-detail p {
    margin-bottom: 5px;
  }

  #APmainCont {
    margin: 30px 0;
    text-align: left;
  }

  .APpara {
    padding: 0 20px;
    text-align: left;
    margin-bottom: 30px;
  }

  #APstep2 {
    margin-bottom: 30px;
  }

  #JoinNow {
    width: 220px;
  }
}

@media all and (max-width: 830px) {
  .affliate-cont {
    min-height: 30%;
    padding: 0 2rem;
  }

  .vendor-title {
    margin: 1rem 1rem;
    font-weight: 400;
    font-size: 15px;
  }

  .vendor-affiliate-detail {
    flex-direction: column;
  }

  .vendor-affiliate-detail p {
    margin-bottom: 5px;
  }

  #APmainCont {
    margin: 30px 0;
    text-align: left;
  }

  .APpara {
    padding: 0 20px;
    text-align: left;
    margin-bottom: 30px;
  }

  #APstep2 {
    margin-bottom: 30px;
  }

  #JoinNow {
    width: 220px;
  }
}
